<template>
    <el-container>
        <el-header>
            {{name}}Header
            <div class="logout" @click="logout">
                退出
            </div>

        </el-header>
        <el-container class="main">
            <el-aside width="200px">
                <Menu></Menu>
            </el-aside>
            <el-main>

                <router-view> </router-view>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import Menu from "@/components/menu"
    export default {
        name: 'hello',
        data () {
            return {
                name:this.$store.state.aname
            }
        },
        components:{Menu},
        methods:{
            logout(){
                this.$store.commit("setLogin","");
                this.$router.push("/login");
            }
        }
    }
</script>

<style>
    .el-header, .el-footer {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        height:10vh;
    }
    .main{
        height:90vh;
    }

    .el-aside {
        background-color: #D3DCE6;
        color: #333;
        text-align: center;
        line-height: 200px;
        height:100%;
    }

    .el-main {
        background-color: #E9EEF3;
        color: #333;

        height:100%;
    }


    .el-container:nth-child(5) .el-aside,
    .el-container:nth-child(6) .el-aside {
        line-height: 260px;
    }

    .el-container:nth-child(7) .el-aside {
        line-height: 320px;
    }
</style>


