<template>
         <div class="user-box">
             <div>
                 批量插入:
                 <el-upload
                         class="upload-demo"
                         action="/api/upUser"
                         :file-list="fileList"
                         :on-success="success"
                 >
                     <el-button size="small" type="primary">点击上传</el-button>
                     <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                 </el-upload>
             </div>

             <div>
             选择所属部门:

             <router-view>


             </router-view>
             </div>

             <el-form ref="form"  label-width="80px">
                 <el-form-item label="员工姓名:">
                     <el-input v-model="uname" :disabled="disable"></el-input>
                 </el-form-item>
                 <el-form-item label="员工电话:">
                     <el-input v-model="phone" :disabled="disable"></el-input>
                 </el-form-item>

                 <el-form-item>
                     <el-button type="primary" @click="onSubmit">添加</el-button>
                     <el-button>取消</el-button>
                 </el-form-item>
             </el-form>



         </div>



</template>

<script>
    export default {
        name: 'hello',
        data () {
            return {
                uname:"",
                phone:"",
                fileList: []
            }
        },
        methods:{
            onSubmit(){
                var pid=this.$store.state.userPartId;
                var uname=this.uname;
                var phone=this.phone;
                var params="pid="+pid+"&uname="+uname+"&phone="+phone;
                fetch("/api/addUserCon?"+params).then(function (e) {
                    return e.text();

                }).then( (e)=> {
                    if(e=="ok"){
                        this.$message({
                            message: '成功了',
                            type: 'success'
                        });
                    }else{
                        this.$message.error('失败');
                    }
                })
            },
            success(info){
                console.log(info);
            }
        },
        computed:{
            disable(){
                return this.$store.state.disable
            }

        }
    }
</script>

<style scoped>
.user-box{
    width:300px;

}
    form{
        margin-top:30px;
    }
</style>


