<template>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
         <span>{{message}}</span>
        <el-form-item label="用户名" prop="user">
            <el-input type="text" v-model="ruleForm2.user" auto-complete="off"></el-input>
        </el-form-item>


        <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
        </el-form-item>



        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>


            <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
    </el-form>

</template>
<script>

    export default {
        data() {
            var checkuser=(rule,value,callback)=>{
                 if(value===""){
                     callback(new Error('请输入用户名'));
                 }else{
                     callback();
                 }
            }

            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                }else{
                    callback();
                }
            };
            return {
                ruleForm2: {
                    user: '',
                    pass: ''
                },
                rules2: {
                    user: [
                        { validator: checkuser, trigger: 'blur' }
                    ],
                    pass: [
                        { validator: validatePass, trigger: 'blur' }
                    ]
                },
                message:""
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        fetch("/api/checkLogin",{
                            method:"post",
                            headers:{"content-type":"application/x-www-form-urlencoded"
                            },
                            body:"user="+this.ruleForm2.user+"&pass="+this.ruleForm2.pass
                        }).then(function (e) {
                            return e.json();
                        }).then( (e)=> {
                    if(e.message=="ok"){
                       this.$store.commit("setLogin","yes");
                        this.$store.commit("setAname",e.data[0].aname);
                        this.$store.commit("setAid",e.data[0].aid);
                        this.$store.commit("setRid",e.data[0].rid);
                        this.$router.push("/")
                    }else{
                        this.message="用户名或密码错误"
                    }
                        });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style scoped>

</style>


