
<script>

    export  default{
        data(){
            return {
                data: [],
                tel:[],
                str:"str111"

            }
        },

        created(){
            fetch("/api/selectPart").then(function (e) {
                return e.json();

            }).then( (e)=> {
                var arr=[];
                function tree(e,num,arr){
                    for(var i=0;i<e.length;i++){
                        var obj={};
                        if(e[i].parentid==num){
                            obj.label=e[i].pname;
                            obj.pid=e[i].pid;
                            obj.children=[];
                            obj.parentid=e[i].parentid;
                            arr.push(obj)
                            tree(e,e[i].pid,obj.children)
                        }
                    }
                }
                tree(e,0,arr);
                this.data=arr;

                this.$store.commit("setPartFullInfo",arr);
                this.create();

            })

        },
        render(h){

           return (
                   <div>
                       <table class="table table-bordered part" >
                           <tr>
                               <th>部门id</th>
                               <th>部门名称</th>
                               <th>操作</th>
                           </tr>

                           <tbody>
                           {this.tel}
                           </tbody>
                       </table>
                   </div>
           )
        },
        methods:{

            edit(data){
                this.$store.commit("setPartInfo",data);
                this.$router.push("/editPart");
            },

            del(data){
                var that=this;
                function delEle(origin,curr){
                    for(var i=0;i<origin.length;i++){
                        if(origin[i].pid==curr.pid){
                            if(origin[i].children&&origin[i].children.length>0){
                                    alert("不能删除");
                            }else {
                                //1. 删除数据库的数据

                                ( (i)=> {

                                    fetch("/api/delPart?pid="+curr.pid).then(function (e) {
                                        return e.text();
                                    }).then( (e)=> {
                                        if(e=="ok"){
                                            //2. 删除内存的数据
                                            origin.splice(i, 1);

                                            that.create();
                                        }else{
                                            alert("错误");
                                        }

                                    })

                                })(i)

                                

                            }
                            break;
                        }else{
                            if(origin[i].children&&origin[i].children.length>0) {
                                delEle(origin[i].children, curr);
                            }
                        }
                    }
                }

                delEle(that.data,data);


            },
            create(){
                var tel=[];
                var flag="-";
                var that=this;
                function getInfo(arr,flag,step) {
                    for(var i=0;i<arr.length;i++){

                        if(arr[i].parentid==0){
                            step=0;
                        }
                        var spaces=[];
                        for(var j=0;j<step;j++){
                            spaces.push(<span>&nbsp;&nbsp;</span>)
                        }
                        var flags=flag.repeat(step);

                       tel.push(<tr><td>{arr[i].pid}</td><td class="pname">{spaces}{flags+arr[i].label}</td><td><span onClick={((obj)=>{
                           return ()=>{
                               that.del(obj);
                           }
                       })(arr[i])}>删除</span>

                       <span onClick={((obj)=>{
                           return ()=>{
                               that.edit(obj);
                           }
                       })(arr[i])}>编辑</span>

                       </td></tr>);

                        if(arr[i].children&&arr[i].children.length>0){
                            step++;
                            getInfo(arr[i].children,flag,step);

                            step--
                        }
                    }
                }


                getInfo(this.data,flag,0);
                this.tel=tel;

            }
        }


    }



</script>

<style scoped>

</style>


