<template>
       <el-table
               :data="tableData"
               style="width: 100%">
           <el-table-column
                   prop="aid"
                   label="id"
                   width="180">
           </el-table-column>
           <el-table-column
                   prop="aname"
                   label="用户名"
                   width="180">
           </el-table-column>
           <el-table-column
                   prop="photo"
                   label="头像">

               <template slot-scope="scope">
                   <img :src="'http://localhost:8888/'+scope.row.photo" alt="" width="100">
               </template>
           </el-table-column>
           <el-table-column
                   prop="rname"
                   label="角色">

           </el-table-column>


           <el-table-column>
               <template slot-scope="scope">
                   <el-button v-show="isShowEditBtn"
                              size="mini"
                              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                   <el-button v-show="isShowDelBtn"
                              size="mini"
                              type="danger"
                              @click="handleDelete(scope.$index, scope.row)">删除</el-button>

                   <el-button
                           size="mini"
                           type="danger"
                           >

                       <router-link :to="'/editPass/'+scope.row.aid">修改密码</router-link>

                   </el-button>
               </template>
           </el-table-column>
       </el-table>
</template>

<script>
    export default {

            data() {
                return {
                    tableData: [],
                    isShowEditBtn:false,
                    isShowDelBtn:false
                }
            },
          created(){
                var params="aid="+this.$store.state.aid+"&rid="+this.$store.state.rid;
               fetch("/api/showAdmin?"+params).then(function (e) {
              return e.json();
               }).then((e)=>{
                   this.tableData=e.data;
                   if(this.$store.state.rid==3){
                       this.isShowEditBtn=true;
                       this.isShowDelBtn=true;
                   }else{
                       var lids=e.lid;
                       console.log(lids);
                       this.isShowDelBtn= lids.indexOf("1")>-1?true:false;
                       this.isShowEditBtn= lids.indexOf("3")>-1?true:false;
                   }
               })
          }
        }

</script>

<style scoped>

</style>


