<template>
  <div>
       {{message}}

      <el-form ref="form"  label-width="80px">

          <el-form-item label="所属部门">
              <span @click="first">一级部门</span>
              <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
      </el-select>
          </el-form-item>

          <el-form-item label="部门名称">
      <el-input v-model="pname" placeholder="请输入内容" class="ele-input"></el-input>
          </el-form-item>


          <el-form-item>
              <el-button type="primary" @click="onSubmit">添加</el-button>
              <el-button>取消</el-button>
          </el-form-item>
      </el-form>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                message:"",
                pname:"",
                data: [],
                parentid:0,
                currentData:[],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            };
        },
        created(){

            fetch("/api/selectPart").then(function (e) {
                return e.json();
                
            }).then( (e)=> {
                var arr=[];
                function tree(e,num,arr){
                    for(var i=0;i<e.length;i++){
                        var obj={};
                        if(e[i].parentid==num){
                            obj.label=e[i].pname;
                            obj.pid=e[i].pid;
                            obj.children=[];
                            arr.push(obj)
                            tree(e,e[i].pid,obj.children)
                        }
                    }
                }
                tree(e,0,arr);
                this.data=arr;
                this.currentData=arr;

                console.log(this.currentData);
            })
        },
        methods: {
            first(){

                this.parentid=0;
                this.currentData=this.data;
                console.log(this.currentData);
            },
            handleNodeClick(data) {
                this.currentData=data;
                this.parentid=data.pid;
                console.log(this.parentid);
            },
            onSubmit(){
                var params="parentid="+this.parentid+"&pname="+this.pname
                fetch("/api/addPart?"+params).then(function (e) {
                    return e.text();       
                }).then((e)=> {
                    if(e!="err"){
                        this.message="添加成功";



                      /*  function getTree(arr,pid){
                            for(var i=0;i<arr.length;i++){
                                if(arr[i].pid==pid){
                                    return arr[i];
                                }else{
                                   return getTree(arr[i].children,pid)
                                }
                            }
                        }*/
                        var obj={label:this.pname,pid:e,children:[]};
                        if(this.parentid==0){
[]
                            this.currentData.push(obj);
                        }else {
                            this.currentData.children.push(obj);
                        }
                        this.pname="";
                    }else{
                        this.message="添加失败"
                    }
                })
            }
        }
    };
/*


    [

        {pid:1,pname:"aa中心",parentid:0},
        {pid:2,pname:"bb中心",parentid:0},
        {pid:3,pname:"cc中心",parentid:0},
        {pid:4,pname:"dd中心",parentid:0},
        {pid:5,pname:"aa部门",parentid:1},
    ]
  */
    var arr=[
        {
            pid:1,label:"aa中心",children:[

            {pid:5,lable:"aa部门",children:[]}
        ]
        },
        {pid:2,label:"bb中心",parentid:0,children:[]},
        {pid:3,label:"cc中心",parentid:0,children:[]},
        {pid:4,label:"dd中心",parentid:0,children:[]},
        ]
</script>

<style scoped>

</style>


