<template>
    <div>

    <el-card class="box-card">
        <i class="el-icon-back" @click="back" v-show="isshow"></i>
        <div v-for="item in data" :key="item.pid" class="text item">
            <router-link :to="'/list/'+item.pid" v-if="item.son">
                {{item.pname}}
            </router-link>
            <span v-if="!item.son" @click="select(item)" :style="item.style">
                {{item.pname}}
            </span>

        </div>
    </el-card>
    </div>
</template>

<script>
    export default {
        name: 'hello',
        data () {
            return {
                data:[
                ],
                pid:"",
                style:{}
            }
        },
        methods:{
            back(){
                this.$router.back();
            },
            select(obj){

                this.data.forEach(function (item) {
                    item.style={};
                })
                obj.style={color:"red"};

                this.$store.commit("setUserPartId",obj.pid);
                this.$store.commit("setDisable",false);
            }

        },
        created(){
            this.$store.commit("setDisable",true);
            var pid=this.$route.params.pid||0;
            fetch("/api/userPart?pid="+pid).then(function (e) {
                return e.json();
            }).then((e)=> {
                this.data=e;
            })
        },
        watch:{
            $route(){
                this.$store.commit("setDisable",true);
                var pid=this.$route.params.pid||0;
                fetch("/api/userPart?pid="+pid).then(function (e) {
                    return e.json();
                }).then((e)=> {
                    this.data=e;
                })
            }
        },
        computed:{
            isshow(){
                if(this.$route.path=="/addUser"){
                    return false
                }else{
                    return true
                }
            }
        }
    }
</script>

<style scoped>

</style>


