var obj={
    install(v,params){
        v.prototype.getSession=function (attr) {
            return sessionStorage.getItem(attr);
        }

        v.prototype.setSession=function (attr,val) {
           sessionStorage.setItem(attr,val);
        }
    }
}
export default obj;



