<template>
    <div class="editPass">
        {{message}}
        <div class="group">
            <label for="old">
                输入原始密码
            </label>
            <input type="text" v-model="old" id="old">
        </div>

        <div class="group">
            <label for="news">
                输入新密码
            </label>
            <input type="text" v-model="news" id="news">
        </div>

        <div class="group">
            <label for="again">
                确认密码
            </label>
            <input type="text" v-model="again" id="again">
        </div>

        <div class="group">
            <div class="btn" @click="submit">提交</div>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'hello',
        data () {
            return {
                old: "",
                news: "",
                again: "",
                message: ""
            }
        },
        methods: {
            submit(){
                if (this.old == "") {
                    this.message = "用户名不能为空";
                    return;
                }
                if (this.news == "") {
                    this.message = "密码不能为空";
                    return;
                }

                if ( this.again=="") {
                    this.message = "再次输入密码";
                    return;
                }

                if (this.news !== this.again) {
                    this.message = "两次密码要一致";
                    return;
                }

                this.message="符合规范";

                var params="old="+this.old+"&news="+this.news+"&aid="+this.$store.state.aid;
                fetch("/api/editPass?"+params).then(function (e) {
                   return  e.text();
                }).then((e)=> {
                    if(e==="修改成功"){
                        this.$store.commit("setLogin","");
                        this.$store.commit("setAname","");
                        this.$store.commit("setAid","");
                        this.$store.commit("setRid","");
                        this.$router.push("/login")
                    }
                    this.message=e;
                    this.old="";
                    this.news="";
                    this.again="";
                })
            }
        }
    }
</script>

<style scoped>
    .editPass {
        width: 100%;
        height: auto;
    }

    .editPass .group {
        width: 100%;
        height: auto;
        padding: 20px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-around;
    }

    .group label {
        width: 30%;
        text-align: center;
    }

    .group input[type=text] {
        height: 45px;
        width: 70%;
        border: 1px solid #000;
        border-radius: 5px;
    }

    .group input:focus {
        outline: none;
        border: 1px solid red;
    }

    .group .btn {
        display: inline-block;
        padding: 5px 10px;
        border: 1px solid #000;
        background: linear-gradient(#fff, #eee);

    }

    .group .btn:hover {
        box-shadow: 0 0 5px #888;
    }


</style>


