import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
var store=new Vuex.Store({
    state: {
        login: "",
        aid:"",
        rid:"",
        aname:"",
        levelInfo:"",
        roleInfo:"",
        partInfo:"",
        partFullInfo:"",
        disable:true,
        userPartId:""
    },
    mutations: {
        setLogin (state,val) {
            state.login=val;
        },
        setLevalInfo(state,val){
            state.levelInfo=val;
        },
        setAid(state,val){
            state.aid=val;
        },
        setRid(state,val){
            state.rid=val;
        },
        setAname(state,val){
            state.aname=val;
        },
        setRoleInfo(state,val){
            state.roleInfo=val;
        },
        setPartInfo(state,val){
            state.partInfo=val;
        },
        setPartFullInfo(state,val){
            state.partFullInfo=val;
        },
        setDisable(state,val){
            state.disable=val;
        },
        setUserPartId(state,val){
            state.userPartId=val;
        }
    }
})

export default  store;


