<template>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <span>{{message}}</span>
        <el-form-item label="权限名称" prop="lname">
            <el-input type="text" v-model="ruleForm2.lname" auto-complete="off"></el-input>
        </el-form-item>


        <el-form-item label="权限对应的值" prop="lnum">
            <el-input type="text" v-model="ruleForm2.lnum" auto-complete="off"></el-input>

        </el-form-item>

        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>

            <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
    </el-form>

</template>
<script>
 export default {
        data() {

            var checkuser=(rule,value,callback)=>{
                if(value===""){
                    callback(new Error('请输入权限名字'));
                }else{
                    callback();
                }
            }

            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入权限值'));
                }else{
                    callback();
                }
            };

            return {
                ruleForm2: {
                    lname: '',
                    lnum: ''
                },
                lname1:"",
                lnum1:"",
                rules2: {
                    lname: [
                        { validator: checkuser, trigger: 'blur' }
                    ],
                    lnum: [
                        { validator: validatePass, trigger: 'blur' }
                    ]
                },
                message:"",
                checkedCities1: ['上海', '北京'],
                cities: ['上海', '北京', '广州', '深圳']
            };
        },
        created(){

            var info=this.$store.state.levelInfo;

            this.ruleForm2.lname=(info.lname);
            this.ruleForm2.lnum=info.lnum;
            this.lname1=(info.lname);
            this.lnum1=info.lnum;

            //多个组件之间数据如何传递
            //1.  params
            //2.  query
            //3.  vuex  处理状态  vue  数据  多组件共享数据的问题
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        if(!(this.ruleForm2.lname==this.lname1&&this.ruleForm2.lnum==this.lnum1)){
                            var params="lid="+this.$store.state.levelInfo.lid+"&lname="+this.ruleForm2.lname+"&lnum="+this.ruleForm2.lnum
                             fetch("/api/editLevelSubmitCon?"+params).then(function (e) {
                              return e.text();
                             }).then( (e)=> {
                                 if(e=="ok"){
                                     this.message="成功";
                                 }else{
                                     this.message="失败";
                                 }
                             })


                        }else{
                           this.message="没有进行修改";
                        }
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style scoped>

</style>


