<template>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <span>{{message}}</span>
        <el-form-item label="角色的名字" prop="rname">
            <el-input type="text" v-model="ruleForm2.rname" auto-complete="off"></el-input>
        </el-form-item>


        <el-form-item label="角色的权限" prop="pass">

            <el-checkbox-group
                    v-model="checkedCities1" :min=1>
                <el-checkbox v-for="item in levels" :label="item.lnum" :key="item.lid">{{item.lname}}</el-checkbox>

            </el-checkbox-group>

        </el-form-item>



        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>

            <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
    </el-form>

</template>
<script>

    export default {
        data() {
            var checkuser=(rule,value,callback)=>{
                if(value===""){
                    callback(new Error('请输入角色名字'));
                }else{
                    callback();
                }
            }

            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                }else{
                    callback();
                }
            };
            return {
                ruleForm2: {
                    rname: '',
                    pass: ''
                },
                rules2: {
                    rname: [
                        { validator: checkuser, trigger: 'blur' }
                    ]
                },
                message:"",
                checkedCities1: [],
                levels: [],
            };
        },

        created(){
            var ajax=new XMLHttpRequest();
            ajax.onload= ()=> {
               this.levels=(JSON.parse(ajax.response).data);
            }
            ajax.open("get","/api/showLevel");
            ajax.send();
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        var params="lname="+this.ruleForm2.rname+"&lnum="+JSON.stringify(this.checkedCities1);
                        console.log(params);
                        fetch("/api/addRoleInfo?"+params).then(function (e) {
                            return e.text();
                            
                        }).then( (e)=> {
                                if(e=="ok"){
                                    this.message="成功";
                                }else{
                                    this.message="失败";
                                }
                        })

                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style scoped>

</style>


