<template>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <span>{{message}}</span>
        <el-form-item label="角色的名字" prop="rname">
            <el-input type="text" v-model="ruleForm2.rname" auto-complete="off"></el-input>
        </el-form-item>


        <el-form-item label="角色的权限" prop="pass">

            <el-checkbox-group
                    v-model="checkedCities1" :min=1>
                <el-checkbox v-for="item in levels" :label="item.lnum" :key="item.lid">{{item.lname}}</el-checkbox>

            </el-checkbox-group>

        </el-form-item>



        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>

            <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
    </el-form>

</template>
<script>

    export default {
        data() {
            var checkuser=(rule,value,callback)=>{
                if(value===""){
                    callback(new Error('请输入角色名字'));
                }else{
                    callback();
                }
            }

            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                }else{
                    callback();
                }
            };
            return {
                ruleForm2: {
                    rname: '',
                    pass: ''
                },
                rules2: {
                    rname: [
                        { validator: checkuser, trigger: 'blur' }
                    ]
                },
                message:"",
                checkedCities1: [],
                levels: [],
            };
        },

        created(){
            var info=this.$store.state.roleInfo;
            this.ruleForm2.rname=info.rname;
            this.levels=info.levels;
            for(var i in info.lid){
                this.checkedCities1.push(info.lid[i].lid);
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        /*修改role信息*/
                        var rid=this.$store.state.roleInfo.rid;
                        var rname=this.ruleForm2.rname
                        var checkInfo=this.checkedCities1.join(",");

                        var params="rid="+rid+"&rname="+rname+"&lid="+checkInfo;

                       fetch("/api/editRole?"+params).then(function (e) {
                           return e.text();
                       }).then((e) =>{
                           if(e=="ok"){
                               this.message="修改成";
                           }else{
                               this.message="修改失败";
                           }
                       })



                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style scoped>

</style>


