<template>
    <div>
         {{message}}
<form>
        <div class="form-group">
            <label for="aname">用户名</label>
            <input type="text" class="form-control" id="aname" placeholder="用户名" v-model="aname">
        </div>

        <div class="checkbox">
            <label v-for="item in roles">
                <input type="radio" :value="item.rid" v-model="role" name="role"> {{item.rname}}
            </label>
        </div>
        <input type="hidden" name="photo" v-model="photo">
        <button type="button" class="btn btn-default" @click="submit">Submit</button>
    </form>

     上传头像：
    <el-upload
                action="/api/upload"

                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
    </div>


</template>

<script>
    require("@/assets/bootstrap/dist/css/bootstrap.min.css")
    export default {
        data() {
            return {
                imageUrl: '',
                role:"",
                roles:[],
                aname:"",
                photo:"",
                message:""
            };
        },
        created(){
            fetch("/api/selectRole").then(function (e) {
              return e.json();
            }).then( (e)=> {
                this.roles=e;
            })

        },
        methods: {
            submit(){
              var params="aname="+this.aname+"&apass=123456&rid="+this.role+"&photo="+this.photo;

              fetch("/api/addAdmin",{
                  method:"post",
                  headers:{"content-type":"application/x-www-form-urlencoded"},
                body:params
              }).then(function (e) {
              return e.text();
              }).then((e)=> {
                  if(e=="ok"){
                       this.message="成功";
                  }else{
                        this.message="失败";
                  }
                  this.aname="";
                  this.role="";
                  this.photo="";
              })
            },
            handleAvatarSuccess(res, file) {
                console.log(file);
                this.photo=file.response;
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            }
        }
    }
</script>

<style scoped>

    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #000;
    }
    .el-upload__input[type=file]{
        display: none;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }



    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }

</style>


