<template>

    <el-table
            :data="tableData"
            style="width: 100%">
        <el-table-column
                prop="lid"
                label="id"
                width="180">
        </el-table-column>
        <el-table-column
                prop="lname"
                label="权限描述"
                width="180">
        </el-table-column>
        <el-table-column
                prop="lnum"
                label="权限值">
        </el-table-column>

        <el-table-column label="操作">
            <template slot-scope="scope">
                <el-button
                        size="mini"
                        @click="handleEdit(scope.row)">编辑</el-button>
                <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.row)">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>

    export default {
        name: 'hello',
        data () {
            return {
                tableData: []
            }
        },
        created(){
            fetch("/api/showLevel").then(function (e) {
                return e.json();
            }).then((e)=>{
                if(e.message=="ok"){
                    this.tableData=e.data;
                }
            })
        },
        methods:{
            handleEdit(data){
                //1.  直接跳转 参数

                this.$router.push({path:"/editLevel"});
                this.$store.commit("setLevalInfo",data);

            },
            handleDelete(data){
                /* 1. 数据库数据删掉
                 * 2. 删除 页面当中的内容
                 *   datatable
                  * */
                var ok=window.confirm("确定删除?");
                if(ok){
                    fetch("/api/delLevel/"+data.lid).then(function (e) {
                        return e.text();
                    }).then((e)=> {
                        if(e=="ok"){
                            this.tableData=this.tableData.filter(function (item) {
            if(item.lid!=data.lid){
                return item;
            }
                            })
                        }else{
                            alert("删除失败");
                        }
                    })
                }

            }
        }
    }
</script>

<style scoped>

</style>


