<template>
    <el-table
            :data="tableData"
            style="width: 100%">
        <el-table-column
                prop="rid"
                label="id"
                width="180">
        </el-table-column>
        <el-table-column
                prop="rname"
                label="角色名称"
                width="180">
        </el-table-column>
        <el-table-column
                label="权限值">
            <template slot-scope="scope">
                <span v-for="item in scope.row.lid">
                    {{item.lname}}
                </span>
            </template>
        </el-table-column>

        <el-table-column label="操作">
            <template slot-scope="scope">
                <el-button
                        size="mini"
                        @click="handleEdit(scope.row)">编辑</el-button>
                <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.row)">删除</el-button>
            </template>
        </el-table-column>

    </el-table>
</template>

<script>
    export default {
        name: 'hello',
        data () {
            return {
                tableData: []
            }
        },
        created(){

            fetch("/api/showRole").then(function (e) {
                return e.json();
            }).then((e)=>{
                console.log(e);
                    this.tableData=e;

            })
        },
        methods:{
            handleEdit(row){
                this.$store.commit("setRoleInfo",row);
                this.$router.push("/editRole");

            },
            handleDelete(row){
                var yes=window.confirm("确定要删除吗?");
                if(yes) {
                    var rid = row.rid;
                    var params="aid="+this.$store.state.aid+"&rid="+rid;
                    //1. 删除数据库的数据
                    fetch("/api/delRole?"+params).then(function (e) {
              return e.text();
                    }).then((e)=> {
                        if(e=="ok"){
                          this.tableData=  this.tableData.filter(function (item) {
                 if(item.rid!=row.rid){
                     return item;
                 }
                            })

                        }else{
                            alert("失败");
                        }
                    })

                }
            }
        }
    }
</script>

<style scoped>

</style>


