<template>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <span>{{message}}</span>
        <el-form-item label="权限名称" prop="lname">
            <el-input type="text" v-model="ruleForm2.lname" auto-complete="off"></el-input>
        </el-form-item>


        <el-form-item label="权限对应的值" prop="lnum">
            <el-input type="text" v-model="ruleForm2.lnum" auto-complete="off"></el-input>

        </el-form-item>



        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>

            <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
    </el-form>

</template>
<script>

    export default {
        data() {
            var checkuser=(rule,value,callback)=>{
                if(value===""){
                    callback(new Error('请输入权限名字'));
                }else{
                    callback();
                }
            }

            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入权限值'));
                }else{
                    callback();
                }
            };
            return {
                ruleForm2: {
                    lname: '',
                    lnum: ''
                },
                rules2: {
                    lname: [
                        { validator: checkuser, trigger: 'blur' }
                    ],
                    lnum: [
                        { validator: validatePass, trigger: 'blur' }
                    ]
                },
                message:"",
                checkedCities1: ['上海', '北京'],
                cities: ['上海', '北京', '广州', '深圳']
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {

                      var params="lname="+this.ruleForm2.lname+"&lnum="+this.ruleForm2.lnum;
                      fetch("/api/addLevel?"+params).then(function (e) {
                          return e.text();
                      }).then((e)=>{
                          if(e=="ok"){
                              this.message="成功";

                          }else{
                              this.message="失败";
                          }

                          this.ruleForm2.lnum="";
                          this.ruleForm2.lname="";
                      })


                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

<style scoped>

</style>


