import Vue from "vue";
import VueRouter from "vue-router";
import Container from "@/components/container";
import showAdmin from "@/components/showAdmin";
import Login from "@/components/login";
import addRole from "@/components/addRole";
import addLevel from "@/components/addLevel";
import showLevel from "@/components/showLevel";
import showRole from "@/components/showRole";
import editLevel from "@/components/editLevel";
import editRole from "@/components/editRole";
import addAdmin from "@/components/addAdmin";
import editPass from "@/components/editPass";
import addUser from "@/components/addUser";
import addPart from "@/components/addPart";
import showPart from "@/components/showPart";
import session from "@/plugis/sessionStorage"
import editPart from "@/components/editPart"
import one from "@/components/one"
import two from "@/components/two"

Vue.use(VueRouter);
Vue.use(session);
var obj= new VueRouter({
  routes:[
      {
        path:"/",
        component:Container,
          children:[

              {path:"/showAdmin",component:showAdmin},
              {
                  path:"/addRole",
                  component:addRole
              },
              {
                  path:"/addLevel",
                  component:addLevel
              },
              {
                  path:"/showLevel",
                  component:showLevel
              },
              {
                    path:"/showRole",
                    component:showRole
              },
              {
                  path:"/editLevel",
                  component:editLevel
              },
              {
                  path:"/editRole",
                  component:editRole
              },
              {
                  path:"/addAdmin",
                  component:addAdmin
              },
              {
                  path:"/editPass/:id",
                  component:editPass
              },
              {
                  path:"/addUser",
                  component:addUser,
                  children:[
                      {path:"",component:one},
                      {path:"/list/:pid",component:one}
                  ]
              },
              {
                  path:"/addPart",
                  component:addPart
              },
              {
                  path:"/showPart",
                  component:showPart
              },
              {
                  path:"/editPart",
                  component:editPart
              }
          ],
      },
      {
          path:"/login",
          component:Login
      },
      {
          path:"*",
          redirect:"/"
      }
  ]
})


obj.beforeEach(function(to,from,next){
    if(to.path=="/login"){
        next();
    }else {
        if (obj.app.$store&&obj.app.$store.state.login) {
            next();
        } else {
            next("/login");
        }
    }


})

export default obj;




