<template>
  <div>
       {{message}}

      <el-form ref="form"  label-width="80px">

          <el-form-item label="所属部门">
              <span @click="first">一级部门</span>
              <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
      </el-select>

          </el-form-item>

          <el-form-item label="所属部门">
              <el-input v-model="part" placeholder="所属部门" class="ele-input" disabled></el-input>
          </el-form-item>

          <el-form-item label="部门名称">
      <el-input v-model="pname" placeholder="请输入内容" class="ele-input"></el-input>
          </el-form-item>


          <el-form-item>
              <el-button type="primary" @click="onSubmit">添加</el-button>
              <el-button>取消</el-button>
          </el-form-item>
      </el-form>

  </div>
</template>

<script>
    export default {
        data() {
            return {
                message:"",
                pname:"",
                part:"",
                data: [],
                parentid:0,
                currentData:[],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            };
        },
        created(){

            /*
               /dasd?pid=10


            * 1. 哪一条数据
            *
            * 2. 哪一条内容
            *
            *    pid  pname  parentid
            *
            * */
            this.data=this.$store.state.partFullInfo;
            this.currentData=this.$store.state.partInfo;
            this.parentid=this.currentData.parentid;
            var that=this;

            function getPart(data,parentid) {
                if(parentid==0){
                    that.part="一级选项";
                    return;
                }
                for(var i=0;i<data.length;i++){

                 if(data[i].pid==parentid){
                     that.part=data[i].label;
                     break;
                 }else{

                     if(data[i].children&&data[i].children.length>0){
                         getPart(data[i].children,parentid)
                     }
                 }
                }
            }


            this.pname=this.currentData.label;
            getPart(this.data,this.currentData.parentid);

        },
        methods: {
            first(){
                this.part="一级部门"
                this.parentid=0;
                console.log(this.currentData);
            },
            handleNodeClick(data) {
                this.parentid=data.pid;
                this.part=data.label;
            },
            onSubmit(){
                var params="parentid="+this.parentid+"&pname="+this.pname+"&pid="+this.currentData.pid
                fetch("/api/editPart?"+params).then(function (e) {
                    return e.text();       
                }).then((e)=> {
                    if(e=="ok"){
                        this.message="修改成功";
                        
                    }else{
                        this.message="修改失败"
                    }
                })
            }
        }
    };

</script>

<style scoped>

</style>


